@tailwind base;
@tailwind components;
@tailwind utilities;

.modal {
  border: none;
  border-radius: 8px;
  padding: 0;
  background-color: #ffffff; /* 밝은 흰색 배경 */
  color: #2d3748; /* 텍스트 색상은 어두운 톤으로 */
  width: 400px;
  max-width: 90%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); /* 그림자는 은은하게 */
}

.modal-content {
  position: relative;
  padding: 16px;
}

.modal-close-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: transparent;
  border: none;
  font-size: 20px;
  color: #718096; /* 닫기 버튼 기본 색상 */
  cursor: pointer;
  transition: color 0.3s;
}

.modal-close-btn:hover {
  color: #e53e3e; /* 닫기 버튼 hover 시 강조 */
}

.modal-close-btn:focus {
  outline: none;
  box-shadow: 0 0 4px #63b3ed; /* 포커스 시 테두리 */
}
